<template lang="en">
<section>

    <div class="col-md-12 p-0 mt-2">
        <div class="theme-primary partition-full">
            <span class="p-name text-white">{{ $t('TimeSheet') }} </span>
            <span class="p-actions float-right ">
                <a id="AddManually" :class="FormSchema[0].Data[1].value ? '' : 'tt_disabled'" @click="exportToExcel" href="javascript:;" class="p-action-btn text-white "><em class="fa fa-list"></em><br /><span>{{ $t('Export') }}</span></a>
                <a id="AddManually" :class="FormSchema[0].Data[1].value ? '' : 'tt_disabled'" href="javascript:;" class="p-action-btn text-white openWeek" @click="toggleDatePanel"><em class="fa fa-calendar"></em><br /><span>{{ $t(selectedName)
                            }}</span></a>
                <a :class="listCheckbox.length > 0 ? '' : 'tt_disabled'" class="p-action-btn text-white" id="DeleteMultiple" title="" href="javascript:;" data-toggle-tooltip="tooltip" data-original-title="Delete" @click="deleteMultipleRow">
                    <i class="fa fa-trash"></i><br><span>{{ $t('Delete')}}</span>
                </a>

                <!-- <button class="fa fa-trash-o text-danger"></button> -->
            </span>
        </div>
    </div>
    <div v-if="isDatePanelVisible" id="DivTimer_" data-pageaction="" class="DatePopUp divtimer_design p-1 R75" style="display: block;width:125px;" ref="weekModal">
        <table class="table-responsive AssetDatePopUp d-block ">
            <tbody class="daterangebody">
                <tr class="daterangetr">
                    <td class="sep daterangetd" style="width:100%!important">
                        <ul class="date_select">
                            <li class="borderbottom"  @click="getListByDateType('thisWeek', $t('thisWeek'))"><a title="" id="aWeek" class="Week" data-toggle-tooltip="tooltip" data-original-title="This Week">{{
                                        $t('thisWeek') }}</a></li>
                            <li @click="getListByDateType('LastWeek', $t('LastWeek'))"><a  id="aLastWeek" class="LastWeek" title="" data-toggle-tooltip="tooltip" data-original-title="Last Week">{{ $t('LastWeek') }}</a></li>
                        </ul>
                    </td>

                </tr>
            </tbody>
        </table>
    </div>

    <div class="listing py-3">
        <loader :is-visible="isLoading"></loader>
        <dynamic-form class="daterangecontrolHS" lang="en" v-if="DataLoaded" :buttons="buttons1" :schema="FormSchema" v-on:OnSubmit="onSubmit">
            <template v-slot:tgslot-slotdate="{ data }">
                <!--<div class="col-12 input-group timesheetdatepicker bg-white border-0 p-0">
                                        <div class="form-control py-0 px-2">                            
                                            <input id="date_range" v-model="dateselect" value="2023/12/12 - 2023/12/12" name="date_range"
                                                type="date" @input="GetStartEndDates"
                                                class="textfield2 ie_crossicon_none ToDate form-control p-1 hasdatetimepicker notvue"
                                                autocomplete="off" data-toggle="datetimepicker" :title="$t('DateRange')"
                                                :placeholder="$t('DateRange')">
                                        </div>
                                    </div> -->

                <DatePicker class="d-flex col-12 input-group timesheetdatepicker bg-white p-0" v-model="dateselect" :popover="false" @input="GetStartEndDates" :masks="dateMask">
                    <template #default="{ togglePopover, inputValue, inputEvents }">
                        <a id="aDateLable" class="pr-2 text-dark bg-white py-1 float-left" style="cursor:default">{{
                                $t('DateRange') }}: {{ DisplayWeekDate }}</a>
                        <input id="date_range" class="form-control datepicker_HS" :value="inputValue" v-on="inputEvents" />
                        <div class="input-group-append cursor-pointer" v-on:click="togglePopover">
                            <span class="input-group-text">
                                <em class="fa fa-calendar"></em>
                            </span>
                        </div>

                    </template>
                </DatePicker>
            </template>
            <template v-slot:tgslot-slotbutton="{ data }">
                <button class="btn btn-success" @click="FetchData();userSelectValidation()" :title="$t('ShowTimeSheet')">{{
                        $t('ShowTimeSheet')
                        }} </button>
            </template>
            <template v-slot:tgslot-slotstartenddate="{ data }">
                <p style="top:-74px;margin-left: 104px;">{{ DisplayWeekDate }}</p>
            </template>
        </dynamic-form>
        <!-- <dynamic-form lang="en" v-if="DataLoaded" :schema="ListSchema" v-on:OnSubmit="onSubmit">
                                <template v-slot:tgslot-TimeSheetTable="{ data }">
                                    <a id="btnAddNewEntry" class="btn btn-success formbtn mr-2 text-dark" v-on:click="showAddTaskModal"
                                        name="btnAddNewEntry" title='Add New Entry' style="float: right;"><i class="fa fa-plus pr-2"></i>Add
                                        New Entry</a>
                                    -->
        <div class="listing">
            <div class="col-10 mb-2 float-left p-0">
                <!-- <div class="mr-2 d-inline-block">
                                    <span class="legends-purple colorboxsquare"></span>{{ $t('AuditableRecords') }}
                                </div>
                <div class="mr-2 d-inline-block">
                                    <span class="legends_profile colorboxsquare"></span> {{ $t('LeaveHoliday') }}
                                </div>
                <div class="mr-2 d-inline-block">
                                    <span class="legends_low colorboxsquare"></span>{{ $t('UnPaidLeaveRecords') }}
                                </div>
                <div class="mr-2 d-inline-block">
                                        <span class="legends_medium colorboxsquare"></span> {{$t('TimeSheet')}}
                                    </div> 
                <div class="mr-2 d-inline-block">
                                    <span class="legends_high colorboxsquare"></span>{{ $t('HolidayLeaveAuditableRecords') }}
                                </div>
                <div class="mr-2 d-inline-block">
                                    <span class="lg-pausedresumed colorboxsquare"></span>{{ $t('PausedResumedTrackEntries') }}
                                </div>  
                <div class="mr-2 d-inline-block">
                    <span class="lg-orange colorboxsquare"></span>{{ $t('PausedResumedTrackEntries') }}
                </div>
                -->
                <div class="mr-2 d-inline-block">
                    <span class="legends-purple colorboxsquare"></span> {{ $t('LeaveHoliday') }}
                </div>
                <div class="mr-2 d-inline-block">
                    <span class="lg-flaggedentry colorboxsquare"></span>{{ $t('UnPaidLeaveRecords') }}
                </div>
            </div>
            <div class="col-2 mb-2 float-right p-0">
                <a ref="taskModalOpen" :class="sendForApprovalButton && FormSchema[0].Data[1].value ? '' : 'tt_disabled'" id="btnAddNewEntry" class="btn btn-primary formbtn" v-on:click="showAddTaskModal" name="btnAddNewEntry" :title="$t('AddNewEntry')" style="float: right;"><em class="fa fa-plus pr-2"></em>{{
                        $t('AddNewEntry') }}</a>
                <div class="hs_tag3 taskAddModal" v-if="addTaskShow" ref="taskAddModal">
                    <AddTaskTimeSheetModal ref="hs_tag3" v-bind:store="store" v-on:close="addTaskShow = false" v-on:saveTaskEntry="saveTaskEntry" :userId="FormSchema[0].Data[1].value">
                    </AddTaskTimeSheetModal>
                </div>

            </div>

        </div>
        <div class="table-responsive">
            <table class="table table-bordered dt-responsive nowrap mt-0 timesheettable" id="tableData">
                <thead class="thead-bg">
                    <tr>
                        <th class="remove_td align-middle text-center" style="max-width:30px; min-width:30px; width:30px;">
                            <div class="form-check form-check-inline p-0">
                                <div class="custom-control custom-checkbox">
                                    <input :disabled="sendForApprovalButton ? false : true" type="checkbox" :value="'all'" :class="{ 'chkItems custom-control-input': true }" name="DeleteInputs" :id="'allCheck'" @change="allCheckBox" v-model="allCheck" style="display: none;">
                                    <label class="custom-control-label universal-custom-control-label pt-1" :for="'allCheck'"></label>
                                </div>

                            </div>
                        </th>
                        <th class="remove_td align-middle d-none"> </th>
                        <th style="min-width: 200px;">{{ $t('ProjectName') }}</th>
                        <th class="align-middle d-none">{{ $t('TaskName') }}</th>
                        <th class="align-middle d-none">{{ $t('DESCRIPTION')  }}</th>
                        <th class="text-center" v-for="(item, index) in HeaderList" :key="index">
                            {{ getPreviousDay(item) }}
                        </th>
                        <th class="text-center" id="RowTotal">{{ $t('Total') }}</th>
                        <th class="remove_td ">{{ $t('Action') }}</th>
                    </tr>
                </thead>
                <tbody>

                    <tr v-for="(dataItem, dataIndex) in computedTimesheetList" :key="dataIndex">
                        <td class="remove_td tabchild text-center">

                            <div class="form-check form-check-inline p-0">
                                <div class="custom-control custom-checkbox">
                                    <input :disabled="sendForApprovalButton ? false : true" type="checkbox" class="list-checkBox" :class="{ 'chkItems custom-control-input': true }" :value="dataIndex" name="DeleteInputs" :id="'chk_' + dataIndex" @change="selectedValues" v-model="listCheckbox" style="display: none;">
                                    <label class="custom-control-label universal-custom-control-label pt-1" :for="'chk_' + dataIndex"></label>
                                </div>

                            </div>
                        </td>
                        <!-- :class="{ 'table-danger': dataItem['IsValid'] == false }" -->
                        <td class="remove_td align-middle d-none"></td>
                        <!-- <td>{{ dataItem['ProjectName'] }}</td> -->
                        <td class="its_projectnamebase">

                            <span class="float-left w-100">
                                <!-- <a title="" value="-1" style="cursor:default;" class="text-dark" href="#"> -->
                                <span class="font-14 font-weight-medium float-left text-align-left d-flex w-100 its_projectname">
                                    {{ dataItem['ProjectName'] }}<span class="remove_td">:</span>
                                    <span class="text-dark d-block its_taskname ml-1 font-weight-normal">{{
                                            dataItem['TaskDetail'] }}</span>
                                </span>

                                <!-- <span class="text-dark d-block font-14 its_taskdesc text-truncate float-left font-weight-medium w-100">{{dataItem.TaskDescription }}</span> -->
                               <span class="w-100 float-left">
                                <span class="remove_td text-dark font-14 its_taskdesc float-left w-90">
                                    <span class="text-truncate font-weight-400" @click="!dataItem.TaskDescription ? Edit(dataIndex + 1) : ''"><span class="remove_td">{{indexEdit === dataIndex + 1 ? '' : dataItem.TaskDescription }}</span>
                                        <input class="w-100" v-if="dataItem['ProjectId'] ==0 && (!dataItem.TaskDescription || indexEdit === dataIndex + 1)" :placeholder="$t('NoDescription')" @change="editChange(dataItem, dataIndex + 1, $event)" type="text" :value="dataItem['TaskDescription']"  @input="checkDescription($event)"  />
                                        
                                    </span>
                                    <span class="font-weight-400" v-if="indexEdit === dataIndex + 1 && dataItem['ProjectId'] !=0">
                                        <input class="w-100" @change="editChange(dataItem, dataIndex + 1, $event)" type="text" :value="dataItem['TaskDescription']"  @input="checkDescription($event)" :placeholder="$t('NoDescription')" />
                                        
                                    </span>
                                    <span v-if="!dataItem.TaskDescription && dataItem['ProjectId'] ==0" class="required_field"> {{$t('DescRequired')}} </span>
                                </span>

                                <a v-if="sendForApprovalButton" class="float-right text-secondary" @click="Edit(dataIndex + 1)">
                                    <em v-if="(dataItem['ProjectId'] ==0 && (!dataItem.TaskDescription || indexEdit === dataIndex + 1)) || (indexEdit === dataIndex + 1)" class="fa fa-check text-success" :title="$t('Edit')"></em>
                                    <em v-else class="fa fa-pencil" :title="$t('Edit')"></em></a>

                                </span>

                            </span>

                            <!-- </a> -->

                            </span>
                            <!-- <a v-if="sendForApprovalButton" class="float-right text-secondary" @click="Edit(dataIndex + 1)"><em class="fa fa-pencil" :title="$t('Edit')"></em></a> -->

                        </td>
                        <td class="align-middle d-none">{{dataItem['TaskDetail']}}</td>
                        <td class="align-middle d-none">{{ dataItem['TaskDescription'] }}</td>
                        <td class="align-middle" v-for="(item, index) in HeaderList" :key="index" :class="{ 'table-danger': dataItem['IsValid'] != null && dataItem['IsValid'] != 1 && dataItem['IsValid'].Date == item , 'legend_leaves ' : checkLegendsDates(item)}" :title="dataItem['IsValid'] != null && dataItem['IsValid'] != 1 && dataItem['IsValid'].Date == item ? dataItem['IsValid'].Status : ''">
                            <!-- :class="{ 'table-danger': dataItem['IsValid'] !=null && dataItem['IsValid'] !=1 && IsValidDate == item }" -->
                            <input placeholder="00:00" class="form-control text-center" type="text" v-model="dataItem[item]" @change="validateTimeInput(dataItem, item,index)" @focus="setOldValue(item ,$event)">
                            <div class="d-none">{{ dataItem[item] }} </div>
                        </td>
                        <td class="align-middle text-center totalvalue">{{ rowSum(dataItem) }}</td>
                        <td class="remove_td single-action text-center"><a><i class="fa fa-times text-danger" @click="SingleDeleteData(dataIndex)"></i></a> </td>
                    </tr>
                    <tr class="d-none"></tr>
                    <tr class="its_fixedrow" v-for="(dataItem, dataIndex) in LeaveList" :key="dataIndex">
                        <!-- :class="{ 'table-danger': dataItem['IsValid'] == false }" -->
                        <td class="remove_td align-middle "></td>
                        <td class=" align-middle d-none"></td>
                        <td class=" align-middle d-none"></td>
                        <!-- <td>{{ dataItem['ProjectName'] }}</td> -->
                        <td class="align-middle font-weight-medium">{{ dataItem['ProjectName'] }}</td>
                        <td class="remove_td align-middle d-none"></td>
                        <td class="remove_td align-middle d-none"></td>
                        <td class="align-middle" v-for="(item, index) in HeaderList" :key="index" :class="{ 'table-danger': dataItem['IsValid'] != null && dataItem['IsValid'] != 1 && dataItem['IsValid'].Date == item }" :title="dataItem['IsValid'] != null && dataItem['IsValid'] != 1 && dataItem['IsValid'].Date == item ? dataItem['IsValid'].Status : ''">
                            <!-- :class="{ 'table-danger': dataItem['IsValid'] !=null && dataItem['IsValid'] !=1 && IsValidDate == item }" -->
                            <input value="00:00" class="form-control text-center" type="text" v-model="dataItem[item]" disabled="true">
                            <div class="d-none">{{ dataItem[item] }}</div>
                        </td>
                        <td class="align-middle text-center totalvalue">{{ rowSum(dataItem) }}</td>
                        <td class="remove_td single-action text-center"><i class="fa fa-times text-danger d-none" @click="SingleDeleteData(dataIndex)"></i> </td>
                    </tr>
                    <tr class="its_fixedrow" v-for="(dataItem, dataIndex) in computedBreakList" :key="dataIndex">
                        <!-- :class="{ 'table-danger': dataItem['IsValid'] == false }" -->
                        <td class="remove_td align-middle "></td>
                        <td class=" align-middle d-none"></td>
                        <td class=" align-middle d-none"></td>
                        <!-- <td>{{ dataItem['ProjectName'] }}</td> -->
                        <td class="align-middle font-weight-medium">{{ dataItem['ProjectName'] }}</td>
                        <td class="remove_td align-middle d-none"></td>
                        <td class="remove_td align-middle d-none"></td>
                        <td class="align-middle" v-for="(item, index) in HeaderList" :key="index" :class="{ 'table-danger': dataItem['IsValid'] != null && dataItem['IsValid'] != 1 && dataItem['IsValid']?.Date == item }" :title="dataItem['IsValid'] != null && dataItem['IsValid'] != 1 && dataItem['IsValid'].Date == item ? dataItem['IsValid'].Status : ''">
                            <!-- :class="{ 'table-danger': dataItem['IsValid'] !=null && dataItem['IsValid'] !=1 && IsValidDate == item }" -->
                            <input placeholder=""  maxlength="5" value="00:00" @change="validateTimeInput(dataItem, item,index)" @focus="setOldValue(item ,$event)" class="form-control text-center" type="text" v-model="dataItem[item]">
                            <div class="d-none">{{ dataItem[item] }}</div>
                        </td>
                        <td class="align-middle text-center totalvalue">{{ rowSum(dataItem) }}</td>
                        <td class="remove_td single-action text-center"><i class="fa fa-times text-danger d-none" @click="SingleDeleteData(dataIndex)"></i> </td>
                    </tr>
                </tbody>
                <tbody v-if="computedTimesheetList.length == 0 && computedBreakList.length == 0 && LeaveList.length == 0">
                    <tr>
                        <td colspan="11" class="text-center">{{ $t('NoRecordfound') }}</td>
                    </tr>
                </tbody>

                <tfoot v-if="computedTimesheetList.length > 0 || computedBreakList.length > 0 || LeaveList.length > 0">
                    <div style=" display: contents; float: left;width: 100%;">
                        <tr class="its_fixedrow2">
                            <td class="remove_td align-middle d-none"></td>
                            <td class="align-middle "></td>
                            <td class="align-middle d-none"></td>
                            <td>{{ $t('Total') }}:</td>
                            <td class="remove_td align-middle d-none"></td>
                            <td class="remove_td align-middle d-none"></td>
                            <td class="text-center" v-for="(tData, index) in totalData" :key="index">
                            {{ tData.value
                                    }}</td>
                            <td class="text-center">{{ SumOfFooterRows }}</td>
                            <td class="remove_td"></td>
                        </tr>
                    </div>
                </tfoot>
            </table>
        </div>
        <div class="col-lg-12 col-sm-12 p-0 mt-2">
            <a v-if="sendForApprovalButton" :class="FormSchema[0].Data[1].value ? '' : 'tt_disabled'" id="btnSendForApproval" class="btn btn-success formbtn text-light" v-on:click="SaveAsDraft(0, 1)" name="" :title="$t('SendForApproval')" style="float: right;"><i class="fa fa-send pr-2"></i>{{
                    $t('SendForApproval') }} </a>
            <!-- <a id="btnCancel" title="Cancel" class="btn btn-danger d-none"><i class="fa fa-close pr-2"></i><span>Cancel</span></a> -->
            <a v-if="copyToOtherUserButton && GetUserInfo.UserType.toLowerCase()!='nu'" :class="FormSchema[0].Data[1].value ? '' : 'tt_disabled'" id="btnCopyLastWeek" isapprover="0" class="btn btn-warning formbtn mr-2 text-dark" v-on:click="showCopyTimesheet" name="" :title="$t('CopyToOtherUser')" style="float: right;"><i class="fa fa-copy pr-2"></i>{{
                    $t('CopyToOtherUser') }} </a>

            <a v-if="copyLastWeekButton"  :class="FormSchema[0].Data[1].value ? '' : 'tt_disabled'" id="btnCopyLastWeek" isapprover="0" class="btn btn-warning formbtn mr-2 text-dark" v-on:click="CopyLastWeek" name="" :title="$t('CopyLastWeek')" style="float: right;"><i class="fa fa-copy pr-2"></i>{{
                    $t('CopyLastWeek') }} </a>

            <a v-if="saveAsDraftButton" :class="FormSchema[0].Data[1].value ? '' : 'tt_disabled'" id="btnSaveAsDraft" isapprover="0" class="btn btn-warning formbtn text-dark mr-2" v-on:click="SaveAsDraft(1, 0)" name="" :title="$t('SaveAsDraft')" style="float: right;"><i class="fa fa-save pr-2"></i>{{ $t('SaveAsDraft')
                    }}</a>
            <!-- <a id="btnSendForApproval" isapprover="0" class="btn btn-success text-white formbtn mr-2" onclick="" title='SendForApproval'><i class="fa fa-send pr-2"></i>SendForApproval</a> -->
        </div>
    </div>

    <copyTimesheet v-if="openModal" v-on:close="openModal = false" v-bind:timeheet_userId="timeheetselecteduserId"></copyTimesheet>

</section>
</template>

<script>
import DataService from '../../services/DataService';
import {
    getPreviousDay,
    getDateInFormat,
    dateFormat,
    dateAndTimeZone,
    dateTimeFromat
} from "@/assets/common/vue-common.js"
import AddTaskTimeSheetModal from "../TimeTrack/modals/AddTaskTimesheetModal.vue"
import DatePicker from 'v-calendar/lib/components/date-picker.umd'
import copyTimesheet from "../../components/Modal/Timesheet/CopyTimesheet.vue"
import {
    userTimerInfo
} from "../../stores/TimeTrack"
import moment from "moment"
export default {
    components: {
        AddTaskTimeSheetModal,
        DatePicker,
        copyTimesheet
    },
    setup() {
        const store = userTimerInfo()

        return {
            store
        }
    },
    data() {
        return {
            dateMask: {
                input: dateFormat,
            },
            saveAsDraftButton: true,
            copyLastWeekButton: true,
            copyToOtherUserButton: true,
            sendForApprovalButton: true,
            getPreviousDay,
            dateselect: dateAndTimeZone(moment(), dateTimeFromat),
            //formData: [],
            deletedArray: [],
            totalData: [],
            DataLoaded: 0,
            noRecord: false,
            TotalRecords: 0,
            isLoading: false,
            TimesheetList: [],
            BreakList: [],
            LeaveList: [],
            HeaderList: [],
            UserId: null,
            StartDate: null,
            EndDate: null,
            DisplayWeekDate: this.$t('thisWeek'),
            StartWeekDay: null,
            totalSeconds: 0,
            //Headers: [],
            addTaskShow: false,
            openModal: false,
            SumOfRows: [],
            SumOfFooterRows: 0,
            allCheck: '',
            listCheckbox: [],
            FormSchema: [{
                layoutType: "triple",
                Data: [{
                        astype: "SlotField",
                        label: "",
                        name: "slotdate",
                        value: "",
                        placeholder: "Date Range",
                    },
                    {
                        astype: "SelectField",
                        label: "User",
                        name: "user_id",
                        value: "",
                        placeholder: "",

                        config: {
                            options: [],
                            onChange: this.FetchData
                        },
                        validationRules: {
                            //"required": true
                        },
                    },
                    {
                        astype: "SlotField",
                        label: "",
                        name: "slotbutton",
                        value: "",
                        placeholder: "",

                    },
                ]
            }],
            IsValidDate: [],
            isedit: false,
            indexEdit: '',
            carryItems: [],
            oldTaskDesc: [],
            isOldTaskDesc: [],
            privilegeParams: null,
            holidaydates: [],
            isDatePanelVisible: false,
            selectedName: 'thisWeek',
            oldInputValue: [],
            timeheetselecteduserId:0
        }
    },
    created: async function () {
        var VueObj = this;
        this.privilegeParams = `controller=Timesheet&action=`;
        this.DataLoaded = 1;
        await VueObj.GetStartEndDatesNew();
        await VueObj.GetUsersForTimesheet();
        VueObj.FormSchema[0].Data[1].value = VueObj.GetUserInfo.ID;
        await VueObj.FetchData();
        //VueObj.checkHolidayDate()
        //VueObj.buttonShowHide()
        //this.dateselect = moment()
    },
    mounted: async function () {
        var VueObj = this;
        //VueObj.SaveAsDraft();
        document.addEventListener("click", function (e) {

            if (VueObj.isDatePanelVisible == true) {
                var tagsMultiselect = document.querySelector('.date_select');
                var more = document.querySelector('.openWeek')
                if (e.target.parentNode.closest(".DatePopUp") == VueObj.$refs.weekModal || e.target == more || e.target.parentNode == more || e.target.parentNode == tagsMultiselect || e.target.parentNode == e.target.parentNode.closest(".DatePopUp") || e.target.className == 'date_select') return
                VueObj.isDatePanelVisible = false

            }
            if (VueObj.addTaskShow) {
                if (e.target == VueObj.$refs.taskModalOpen || e.target.parentNode == VueObj.$refs.taskModalOpen || e.target == VueObj.$refs.taskAddModal || e.target.parentNode.closest(".taskAddModal") == VueObj.$refs.taskAddModal) {
                    return
                } else {
                    VueObj.addTaskShow = false
                }
            }

        });
    },
    computed: {
        computedTimesheetList() {
            return this.TimesheetList
        },
        computedBreakList() {
            return this.BreakList
        },
        computedLeaveList() {
            return this.LeaveList
        },
        filteredDeletedArray() {
            return this.deletedArray;
        },
        rowSums() {
            
            return this.TimesheetList.map(dataItem => this.rowSum(dataItem));
        },
        footerSums() {
            return this.totalData.map(dataItem => this.footerSums(dataItem));
        }
    },
    methods: {
        userSelectValidation(){
            if(!this.FormSchema[0].Data[1].value){
            this.ShowAlert(this.$t("PleaseSelectUser"), 'warning', true, this.$t('Alert'));
            }
        },
        checkDescription(e) {
            if (e.target.value.match(/^\s*$/)) {
                e.target.value = ''
            }
        },
        timeMask(e) {
            //var e = value.target.value.split(":");
            if ($.inArray(e.keyCode, [46, 8, 9, 27, 13]) !== -1 ||
             // Allow: Ctrl+A, Command+A
            (e.keyCode === 65 && (e.ctrlKey === true || e.metaKey === true)) || 
             // Allow: home, end, left, right, down, up
            (e.keyCode >= 35 && e.keyCode <= 40)) {
                 // let it happen, don't do anything
                 return;
        }
        // Ensure that it is a number and stop the keypress
        if ((e.shiftKey || (e.keyCode < 48 || e.keyCode > 57)) && (e.keyCode < 96 || e.keyCode > 105)) {
            e.preventDefault();
        }
        },
     formateHHMM(e) {
