        convertH2M(timeInHour) {
            var timeParts = timeInHour.split(":");
            return Number(timeParts[0]) * 60 + Number(timeParts[1]);
        },
        setOldValue(index, event) {
            let obj = this.oldInputValue.find((item) => item.index === index)
            if (!obj) {
