        async validateTimeInput(data, item, index) {
            let oldValue = this.oldInputValue.find((res) => res.index == item)
            var regex = /^[0-9][0-9]:[0-5][0-9]$/;
            if (typeof Number(data[item]) == 'number' && data[item] < this.convertH2M("24:01") && !regex.test(data[item])) {
                if (data[item] < 13) {
                    let hours = data[item] > 0 ? `${data[item].toString().length > 1 ? `${data[item]}` : `${0}${data[item]}`}` : '00'
                    data[item] = hours + ':00'
                } else {
                    let format2 = (n) => `0${n / 60 ^ 0}`.slice(-2) + ':' + ('0' + n % 60).slice(-2)
                    data[item] = format2(data[item])
                }

            }
            if (!regex.test(data[item])) {
                this.ShowAlert(this.$t("validTime"), "warning", true, "Alert");
                //alert("Please enter time in HH:MM .");
                data[item] = oldValue.value ? oldValue.value : '00:00';
            }

            await this.SumRowColTotalList();
            //check column sum and if <24 hours then set old value
            var result = this.strToMins("24:01") - this.strToMins(this.totalData[index].value);
            if (result <= 0) {
                data[item] = oldValue.value ? oldValue.value : '00:00';
                await this.SumRowColTotalList();
                //check if column sum <24 then set 00:00
                let newResult = this.strToMins("24:01") - this.strToMins(this.totalData[index].value);
                newResult <= 0 ? data[item] = '00:00' : ''
                this.ShowAlert(this.$t("TotalhoursNotExceed"), "warning", true, "Alert");
            }
            this.oldInputValue = []
            this.SumRowColTotalList();
        },

