        //     VueObj.confirmR("MessageForCopyLastWeekTimeEntries", true, ("MessageForCopyLastWeekTimeEntries"), function () {
        //         let url = `userId=${VueObj.GetUserInfo.ID}&dateFrom=${fDate}&dateTo=${tDate}&selectedUserId=${VueObj.FormSchema[0].Data[1].value}`;
        //         DataService.PasteLastWeekTimesheet(url).then(response => {
        //             VueObj.FetchData();
        //         });
        //     });

        //     // VueObj.confirmR("MessageForCopyLastWeekTimeEntries", true, ("MessageForCopyLastWeekTimeEntries"), function () {
        //     //     let url = `userId=${VueObj.GetUserInfo.ID}&dateFrom=${fDate}&dateTo=${tDate}&selectedUserId=${VueObj.FormSchema[0].Data[1].value}`;
        //     //     DataService.PasteLastWeekTimesheet(url).then(response => {
        //     //         VueObj.FetchData();
