        CopyLastWeek: async function () {
            // if (!GetPrivilege("/Timesheet/PasteLastWeekTimesheet", true, false)) {
            //     return false;
            // }

            var vm = this;
            var fDate = vm.StartDate;
                var tDate = vm.EndDate;
                var msg=''
                let url = `dateFrom=${fDate}&dateTo=${tDate}&userId=${vm.FormSchema[0].Data[1].value}&isEntryExits=${true}`
               await DataService.CopyLastWeekTimesheet(url).then(response => {
               console.log("response",response)
                    if(response.data[0]==1){
                         msg=this.$t("MessageForCopyLastWeekNoTimeEntries")
                    }
                    else{
                         msg=this.$t("MessageForCopyLastWeekTimeEntries")
                    }
     
                })
            this.confirmR(msg, true, (this.$t("Delete") + "  " + this.$t("entry")), function () {
                var fDate = vm.StartDate;
                var tDate = vm.EndDate;
                let url = `dateFrom=${fDate}&dateTo=${tDate}&userId=${vm.FormSchema[0].Data[1].value}&isEntryExits=${false}`
                DataService.CopyLastWeekTimesheet(url).then(response => {
                    vm.FetchData();
                });

            })

        },
        CopyToUserWeekTimesheet: async function (data) {
            var fDate = this.StartDate;
            var tDate = this.EndDate;
            let url = `dateFrom=${fDate}&dateTo=${tDate}&selectedUserId=${data}&timeheet_userId=${this.FormSchema[0].Data[1].value}`
            DataService.CopyToUserWeekTimesheet(url).then(response => {
                if (response.data[0].Code == 0) {
                    this.ShowAlert(this.$t('CopyToUser'), 'success', true, this.$t('Alert'));
                } else if (response.data[0].Code == 1 && response.data[0].Msg == 'TIMESHEETALREADYSUBMITTED') {
                    this.ShowAlert(this.$t('TIMESHEET_SUBMITTED'), 'warning', true, this.$t('Alert'));
                } else if (response.data[0].Code == 1 && response.data[0].Msg == 'DIFFERENTSHIFTS') {
                    this.ShowAlert(this.$t('DIFFERENTSHIFTS'), 'warning', true, this.$t('Alert'));
                }
                
                this.openModal = false
                this.FetchData();
            });
        },
        // SendForApproval:async function(isDraft, isSendForApproval){
        //     var fDate = this.StartDate;
        //     var tDate = this.EndDate;
        //     let url = `dateFrom=${fDate}&dateTo=${tDate}&selectedUserId=${this.FormSchema[0].Data[1].value}`
        //     DataService.SendForApproval(url).then(response => {
